}

.under-construction {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  font-family: Arial, sans-serif;
  background: linear-gradient(90deg, #97298e, #613fdb, #11dada); /* Gradient background */
  background-size: 200% 200%;
  animation: gradientBG 3s infinite alternate; /* Animated gradient effect */
  color: #d815df; /* Text color */
}

@keyframes gradientBG {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}

@keyframes gradientFadeIn {
  0% {
    background-position: 100%;
  }
  100% {
    background-position: 0%;
  }
}

